import salesActions from './actions';
import authActions from '../auth/actions';
//initial state keys === action string values!
const initialState = {
    [salesActions.GET_PRODUCTS]: null,
    [salesActions.GET_CATEGORIES]: null,
    checkoutItems: {},
    checkoutModal: false,
    loading: false,
    checkoutLoading: false,
    error: null,
    success: null,
    info: null,
}

export default function salesReducer(state = initialState, action) {
    switch (action.type) {
        case salesActions.GET_PRODUCTS:
            return { ...state, loading: true, }
        case salesActions.GET_CATEGORIES:
            return { ...state, loading: true, }
        case salesActions.TOGGLE_CHECKOUT_MODAL:
            return {
                ...state,
                checkoutModal: action.payload === undefined ? !state.checkoutModal : action.payload,
            }
        case salesActions.SUBMIT_CHECKOUT_DATA:
            return {
                ...state,
                loading: true,
                checkoutLoading: true,
            }
        case salesActions.GET_ITEMS_SUCCESS:
            if (action.data === salesActions.SUBMIT_CHECKOUT_DATA) {
                return {
                    ...state,
                    checkoutModal: false,
                    checkoutItems: {},
                    loading: false,
                    checkoutLoading: false,
                    error: null,
                    success: "Data has been submitted!"
                }
            }
            return {
                ...state,
                [action.data]: action.payload,
                checkoutItems: {},
                checkoutLoading: false,
                loading: false,
                error: null,
            }
        case salesActions.GET_ITEMS_FAIL:
            if (action.data = salesActions.SUBMIT_CHECKOUT_DATA) {
                return {
                    ...state,
                    checkoutModal: true,
                    loading: false,
                    checkoutLoading: false,
                    error: action.payload,
                }
            }
            if (action.data = salesActions.GET_CATEGORIES) {
                return {
                    ...state,
                    loading: false,
                    error: action.payload
                }
            }
            if (action.data = salesActions.GET_PRODUCTS) {
                return {
                    ...state,
                    loading: false,
                    error: action.payload
                }
            }
            return {
                ...state,
                error: action.payload,
                loading: false
            }
        case salesActions.ADD_ITEMS_TO_CART:
            if (state[salesActions.GET_PRODUCTS][action.payload[action.id].category][action.id].availableStore === 0) { return state }
            if (state.checkoutItems[action.id]) {
                let all = action.payload[action.id].all;
                let newPayload = { ...action.payload };
                delete newPayload[action.id].all;
                return {
                    ...state,
                    checkoutItems: {
                        ...state.checkoutItems,
                        [action.id]: {
                            ...state.checkoutItems[action.id],
                            amount: all ?
                                state[salesActions.GET_PRODUCTS][newPayload[action.id].category][action.id].availableStore + state.checkoutItems[action.id].amount
                                : state.checkoutItems[action.id].amount + 1,
                            available: all ? 0 : parseFloat(state[salesActions.GET_PRODUCTS][action.payload[action.id].category][action.id].availableStore - 1),
                        }
                    },
                    [salesActions.GET_PRODUCTS]: { //whatever is in the reducer
                        ...state[salesActions.GET_PRODUCTS], //gets cloned right back into state.
                        [action.payload[action.id].category]: { //a particular category
                            ...state[salesActions.GET_PRODUCTS][action.payload[action.id].category], //clone stuff inside the category
                            [action.id]: { //the product key
                                ...state[salesActions.GET_PRODUCTS][action.payload[action.id].category][action.id], //the product properties cloned in,
                                availableStore: all ? 0 : parseFloat(state[salesActions.GET_PRODUCTS][action.payload[action.id].category][action.id].availableStore - 1), //deduct items in store
                            }
                        }
                    }
                }
            } else {
                let all = action.payload[action.id].all;
                let newPayload = { ...action.payload };
                delete newPayload[action.id].all;
                return {
                    ...state,
                    checkoutItems: {
                        ...state.checkoutItems,
                        ...newPayload,
                        [action.id]: {
                            ...newPayload[action.id],
                            amount: all ?
                                state[salesActions.GET_PRODUCTS][newPayload[action.id].category][action.id].availableStore
                                : newPayload[action.id].amount,
                            available: all ? 0 : parseFloat(state[salesActions.GET_PRODUCTS][newPayload[action.id].category][action.id].availableStore - 1),
                        }
                    },
                    [salesActions.GET_PRODUCTS]: { //whatever is in the reducer
                        ...state[salesActions.GET_PRODUCTS], //gets cloned right back into state.
                        [newPayload[action.id].category]: { //a particular category
                            ...state[salesActions.GET_PRODUCTS][newPayload[action.id].category], //clone stuff inside the category
                            [action.id]: { //the product key
                                ...state[salesActions.GET_PRODUCTS][newPayload[action.id].category][action.id], //the product properties cloned in,
                                availableStore: all ? 0 : parseFloat(state[salesActions.GET_PRODUCTS][newPayload[action.id].category][action.id].availableStore - 1), //deduct items in store
                            }
                        }
                    }
                }
            }
        case salesActions.REMOVE_ITEMS_FROM_CART:
            let cleancheckoutItems = {
                ...state.checkoutItems,
                [action.id]: {
                    ...state.checkoutItems[action.id],
                    amount: parseFloat(state.checkoutItems[action.id]?.amount - 1),
                    available: parseFloat(state[salesActions.GET_PRODUCTS][action.payload[action.id].category][action.id].availableStore + 1),
                }
            }

            if (+state.checkoutItems[action.id].amount === 1 || action.payload[action.id].delete) {
                delete cleancheckoutItems[action.id];
            }
            return {
                ...state,
                checkoutItems: {
                    ...cleancheckoutItems,
                },
                [salesActions.GET_PRODUCTS]: { //whatever is in the reducer
                    ...state[salesActions.GET_PRODUCTS], //gets cloned right back into state.
                    [action.payload[action.id].category]: { //a particular category
                        ...state[salesActions.GET_PRODUCTS][action.payload[action.id].category], //clone stuff inside the category
                        [action.id]: { //the product key
                            ...state[salesActions.GET_PRODUCTS][action.payload[action.id].category][action.id], //the product properties cloned in,
                            availableStore: action.payload[action.id].delete ?
                                parseInt(state[salesActions.GET_PRODUCTS][action.payload[action.id].category][action.id].availableStore + (action.payload[action.id].amount))
                                : parseInt(state[salesActions.GET_PRODUCTS][action.payload[action.id].category][action.id].availableStore + 1), //increment items in store
                        }
                    }
                }
            }

        case salesActions.CALC_ITEMS_TO_CART:
            let initialAmt = parseFloat(action.payload.amount + action.payload.available);
            return {
                ...state,
                checkoutItems: {
                    ...state.checkoutItems,
                    [action.id]: {
                        ...action.payload,
                        amount: parseFloat(action.value),
                        available: parseFloat(initialAmt - action.value),
                    }
                },
                [salesActions.GET_PRODUCTS]: { //whatever is in the reducer
                    ...state[salesActions.GET_PRODUCTS], //gets cloned right back into state.
                    [action.payload.category]: { //a particular category
                        ...state[salesActions.GET_PRODUCTS][action.payload.category], //clone stuff inside the category
                        [action.id]: { //the product key
                            ...state[salesActions.GET_PRODUCTS][action.payload.category][action.id], //the product properties cloned in,
                            availableStore: parseFloat(initialAmt - action.value), //deduct items in store
                        }
                    }
                }
            }
        case salesActions.CLEAR_ERROR:
            return { ...state, error: null }
        case salesActions.CLEAR_SUCCESS:
            return { ...state, success: null }
        case salesActions.CLEAR_INFO:
            return { ...state, info: null }
        case salesActions.CLEAR_ALL_MESSAGES:
            return {
                ...state,
                info: null,
                success: null,
                error: null,
            }
        case authActions.LOGOUT:
            return initialState;
        default: return state;
    }
}