const handleConn = {
    // :: string values ===  reducer keys (Don't change!)
    //connection checking
    CONNECTION_CHECKING: 'CONNECTION_CHECKING',
    //connection active
    CONNECTION_ACTIVE: "CONNECTION_ACTIVE",
    //connection closed
    CONNECTION_CLOSED: "CONNECTION_CLOSED",

    CLEAR_MESSAGE: "CLEAR_MESSAGE",


    connectionActive: payload => {
        return { type: handleConn.CONNECTION_ACTIVE, payload, }
    },
    connectionClosed: token => {
        return { type: handleConn.CONNECTION_CLOSED, token, }
    },

    connectionChecking: payload => {
        return { type: handleConn.CONNECTION_CHECKING, payload, }
    },

    clearMessage: payload => {
        return { type: handleConn.CLEAR_MESSAGE, payload, }
    },
}

export default handleConn;