import statsActions from './actions';
import authActions from '../auth/actions';
//initial state keys === action string values!
const initialState = {
    [statsActions.GET_NUMBER_TRANSACTIONS]: null,
    [statsActions.GET_NUMBER_CATEGORIES]: null,
    [statsActions.GET_NUMBER_PRODUCTS]: null,
    [statsActions.GET_NUMBER_SERVICES]: null,
    [statsActions.GET_NUMBER_CUSTOMERS]: null,
    [statsActions.GET_NUMBER_VENDORS]: null,
    [statsActions.GET_NUMBER_USERS]: null,
    [statsActions.GET_REVENUE_MONTH]: null,
    [statsActions.GET_REVENUE_PREV_MONTH]: null,
    [statsActions.GET_REVENUE_YTD]: null,
    [statsActions.GET_TRANSACTIONS_USER]: null,
    loading: false,
    error: null,
}

export default function statsReducer(state = initialState, action) {
    switch (action.type) {
        case statsActions.GET_NUMBER_TRANSACTIONS:
            return { ...state, loading: true, }
        case statsActions.GET_NUMBER_CATEGORIES:
            return { ...state, loading: true, }
        case statsActions.GET_NUMBER_PRODUCTS:
            return { ...state, loading: true, }
        case statsActions.GET_NUMBER_SERVICES:
            return { ...state, loading: true, }
        case statsActions.GET_NUMBER_CUSTOMERS:
            return { ...state, loading: true, }
        case statsActions.GET_REVENUE_MONTH:
            return { ...state, loading: true, }
        case statsActions.GET_NUMBER_ITEMS_SUCCESS:
            return {
                ...state,
                [action.data]: action.payload,
                loading: false,
            }
        case statsActions.GET_NUMBER_ITEMS_FAIL:
            return {
                ...state,
                error: action.payload,
                loading: false
            }
        case authActions.LOGOUT:
            return initialState;
        default: return state;
    }
}