import { take, takeEvery, put } from "redux-saga/effects";
import actions from "./actions";
// import axios from "../../config/axios-config/axios-instance";
import { rtdb } from "../../library/firebase/firebase";

function* fetchProductsAsyncBegin() {
    try {;
        let inventory = yield rtdb.ref(`catStore/HeadQuarters/inventory/`).limitToFirst(1);
        yield inventory.on('value', function* myFunc(snapshot) {
             console.log(snapshot.val());
            //  return snapshot.val();
            yield put(actions.getItemsSuccess(snapshot.val(), actions.GET_PRODUCTS));
        });
        // let data = yield take(actions.GET_PRODUCTS_BY_SAGA);
        // const Inventorydata = yield axios.get(`catStore/HeadQuarters/inventory.json?auth=${data.token}`);
        // yield put(actions.getItemsSuccess(data, actions.GET_PRODUCTS));
    }
    catch (error) {
        yield put(actions.getItemsFail(error.message, actions.GET_PRODUCTS))
    }
}

export function* fetchProductsSaga() {
    yield takeEvery(actions.GET_PRODUCTS, fetchProductsAsyncBegin)
}