import vendorActions from './actions';
import authActions from '../auth/actions';
//initial state keys === action string values!
const initialState = {
    [vendorActions.GET_VENDORS]: null,
    loading: false,
    error: null,
}

export default function vendorsReducer(state = initialState, action) {
    switch (action.type) {
        case vendorActions.GET_VENDORS:
            return { ...state, loading: true, }
        case vendorActions.GET_VENDORS_SUCCESS:
            return {
                ...state,
                [action.data]: action.payload,
                loading: false,
            }
        case vendorActions.GET_VENDORS_FAIL:
            return {
                ...state,
                error: action.payload,
                loading: false
            }
        case authActions.LOGOUT:
            return initialState;
        default: return state;
    }
}