import locationsActions from './actions';
import authActions from '../auth/actions';
//initial state keys === action string values!
const initialState = {
    [locationsActions.GET_LOCATIONS]: null,
    loading: false,
    error: null,
}

export default function locationsReducer(state = initialState, action) {
    switch (action.type) {
        case locationsActions.GET_LOCATIONS:
            return { ...state, loading: true, }
        case locationsActions.GET_LOCATIONS_SUCCESS:
            return {
                ...state,
                [action.data]: action.payload,
                loading: false,
            }
        case locationsActions.GET_LOCATIONS_FAIL:
            return {
                ...state,
                error: action.payload,
                loading: false
            }
        case authActions.LOGOUT:
            return initialState;
        default: return state;
    }
}