import actions from './actions';

const initState = { idToken: null,uid: null };

export default function authReducer(state = initState, action) {
  switch (action.type) {
    case actions.LOGIN_SUCCESS:
      return {
        idToken: action.token,
        uid: action.uid,
      };
    case actions.LOGOUT:
      return initState;
    default:
      return state;
  }
}
