import connectionActions from './actions';
import authActions from '../auth/actions';
//initial state keys === action string values!
const initialState = {
    active: false,
    closed: false,
    message: null,
}

export default function connectionReducer(state = initialState, action) {
    switch (action.type) {
        case connectionActions.CONNECTION_CHECKING:
            return {
                ...state,
                active: false,
                closed: false,
                message: 'Checking Connectivity.'
            }
        case connectionActions.CONNECTION_ACTIVE:
            return {
                ...state,
                active: true,
                closed: false,
                message: 'Connection Active'
            }
        case connectionActions.CONNECTION_CLOSED:
            return {
                ...state,
                active: false,
                closed: true,
                message: 'Connection Closed.'
            }
        case connectionActions.CLEAR_MESSAGE:
            return {
                ...state,
                message: null,
            }
        case authActions.LOGOUT:
            return initialState;
        default: return state;
    }
}