import customersActions from './actions';
import authActions from '../auth/actions';
//initial state keys === action string values!
const initialState = {
    [customersActions.GET_CUSTOMERS]: null,
    loading: false,
    error: null,
}

export default function customersReducer(state = initialState, action) {
    switch (action.type) {
        case customersActions.GET_CUSTOMERS:
            return { ...state, loading: true, }
        case customersActions.GET_CUSTOMERS_SUCCESS:
            return {
                ...state,
                [action.data]: action.payload,
                loading: false,
            }
        case customersActions.GET_CUSTOMERS_FAIL:
            return {
                ...state,
                error: action.payload,
                loading: false
            }
        case authActions.LOGOUT:
            return initialState;
        default: return state;
    }
}