const handleLocations = {
    //categories
    GET_LOCATIONS: 'locationList',
    //any items
    GET_LOCATIONS_SUCCESS: "GET_LOCATIONS_SUCCESS",
    GET_LOCATIONS_FAIL: "GET_LOCATIONS_FAIL",
    //add category to db
    ADD_LOCATIONS: "ADD_LOCATIONS_TO_DB",
    //remove category from db
    DELETE_LOCATIONS: "DELETE_LOCATIONS_FROM_DB",
    //update category in db
    UPDATE_LOCATIONS: "UPDATE_LOCATIONS_IN_DB",

    //categories
    getLocations: payload => {
        return { type: handleLocations.GET_LOCATIONS, payload, }
    },
    getLocationsbySaga: token => {
        return { type: handleLocations.GET_LOCATIONS_BY_SAGA, token, }
    },
    //ANY-ITEMS SUCCESS OR FAIL
    getLocationsSuccess: (payload, data) => {
        return {
            type: handleLocations.GET_LOCATIONS_SUCCESS,
            payload: payload,
            data: data,
        }
    },
    getLocationsFail: (payload, data) => {
        return {
            type: handleLocations.GET_LOCATIONS_FAIL,
            payload,
            data: data,
        }
    },

    addLocation: (payload, id) => {
        return {
            type: handleLocations.ADD_LOCATIONS,
            payload,
            id: id,
        }
    },
    deleteLocation: (payload, id) => {
        return {
            type: handleLocations.DELETE_LOCATIONS,
            payload,
            id: id,
        }
    },
    updateLocation: (value, payload) => {
        return {
            type: handleLocations.UPDATE_LOCATIONS,
            payload: payload,
        }
    },

}

export default handleLocations;