const handleCustomers = {
    // :: string values ===  reducer keys (Don't change!)
    //customers
    GET_CUSTOMERS: 'customerList',
    //any items
    GET_CUSTOMERS_SUCCESS: "GET_CUSTOMERS_SUCCESS",
    GET_CUSTOMERS_FAIL: "GET_CUSTOMERS_FAIL",
    //add customer to db
    ADD_CUSTOMER: "ADD_CUSTOMER_TO_DB",
    //remove customer from db
    DELETE_CUSTOMER: "DELETE_CUSTOMER_FROM_DB",
    //update customer in db
    UPDATE_CUSTOMER: "UPDATE_CUSTOMER_IN_DB",

    //CUSTOMERS
    getCustomers: payload => {
        return { type: handleCustomers.GET_CUSTOMERS, payload, }
    },
    getCustomersbySaga: token => {
        return { type: handleCustomers.GET_CUSTOMERS_BY_SAGA, token, }
    },
    //ANY-ITEMS SUCCESS OR FAIL
    getCustomersSuccess: (payload, data) => {
        return {
            type: handleCustomers.GET_CUSTOMERS_SUCCESS,
            payload: payload,
            data: data,
        }
    },
    getCustomersFail: (payload, data) => {
        return {
            type: handleCustomers.GET_CUSTOMERS_FAIL,
            payload,
            data: data,
        }
    },

    addCustomer: (payload, id) => {
        return {
            type: handleCustomers.ADD_CUSTOMER,
            payload,
            id: id,
        }
    },
    deleteCustomer: (payload, id) => {
        return {
            type: handleCustomers.DELETE_CUSTOMER,
            payload,
            id: id,
        }
    },
    updateCustomer: (value, payload) => {
        return {
            type: handleCustomers.UPDATE_CUSTOMER,
            payload: payload,
        }
    },

}

export default handleCustomers;