const handleVendors = {
    // :: string values ===  reducer keys (Don't change!)
    //customers
    GET_VENDORS: 'vendorList',
    //any items
    GET_VENDORS_SUCCESS: "GET_VENDORS_SUCCESS",
    GET_VENDORS_FAIL: "GET_VENDORS_FAIL",
    //add customer to db
    ADD_VENDOR: "ADD_VENDOR_TO_DB",
    //remove customer from db
    DELETE_VENDOR: "DELETE_VENDOR_FROM_DB",
    //update customer in db
    UPDATE_VENDOR: "UPDATE_VENDOR_IN_DB",

    //CUSTOMERS
    getVendors: payload => {
        return { type: handleVendors.GET_VENDORS, payload, }
    },
    getVendorsbySaga: token => {
        return { type: handleVendors.GET_VENDORS_BY_SAGA, token, }
    },
    //ANY-ITEMS SUCCESS OR FAIL
    getVendorsSuccess: (payload, data) => {
        return {
            type: handleVendors.GET_VENDORS_SUCCESS,
            payload: payload,
            data: data,
        }
    },
    getVendorsFail: (payload, data) => {
        return {
            type: handleVendors.GET_VENDORS_FAIL,
            payload,
            data: data,
        }
    },

    addVendor: (payload, id) => {
        return {
            type: handleVendors.ADD_VENDOR,
            payload,
            id: id,
        }
    },
    deleteVendor: (payload, id) => {
        return {
            type: handleVendors.DELETE_VENDOR,
            payload,
            id: id,
        }
    },
    updateVendor: (value, payload) => {
        return {
            type: handleVendors.UPDATE_VENDOR,
            payload: payload,
        }
    },

}

export default handleVendors;