const handleSales = {
    // :: string values ===  reducer keys (Don't change!)
    //products
    GET_PRODUCTS: 'productList',
    //categories
    GET_CATEGORIES: "categoryList",
    //any items
    GET_ITEMS_SUCCESS: "GET_ITEMS_SUCCESS",
    GET_ITEMS_FAIL: "GET_ITEMS_FAIL",

    //add to cart
    ADD_ITEMS_TO_CART: "ADD_ITEMS_TO_CART",
    //remove from cart
    REMOVE_ITEMS_FROM_CART: "REMOVE_ITEMS_FROM_CART",
    //calculate dynamic to cart
    CALC_ITEMS_TO_CART: "CALC_ITEMS_TO_CART",
    //clear cart
    CLEAR_ITEMS_FROM_CART: "CLEAR_ITEMS_FROM_CART",
    //submit data
    SUBMIT_CHECKOUT_DATA: "SUBMIT_CHECKOUT_DATA",
    //checkoutmodal toggle
    TOGGLE_CHECKOUT_MODAL: "TOGGLE_CHECKOUT_MODAL",
    //clear error messages
    CLEAR_ERROR: "CLEAR_ERROR",
    //clear success messages
    CLEAR_SUCCESS: "CLEAR_SUCCESS",
    //clear info messages
    CLEAR_INFO: "CLEAR_INFO",
    //clear all messages
    CLEAR_ALL_MESSAGES: "CLEAR_ALL_MESSAGES",

    //PRODUCTS
    getProducts: payload => {
        return { type: handleSales.GET_PRODUCTS, payload, }
    },
    getProductsbySaga: token => {
        return { type: handleSales.GET_PRODUCTS_BY_SAGA, token, }
    },
    //CATEGORIES
    getCategories: payload => {
        return { type: handleSales.GET_CATEGORIES, payload, }
    },

    //ANY-ITEMS
    itemsSuccess: (payload, data) => {
        return {
            type: handleSales.GET_ITEMS_SUCCESS,
            payload: payload,
            data: data,
        }
    },
    itemsFail: (payload, data) => {
        return {
            type: handleSales.GET_ITEMS_FAIL,
            payload,
            data: data,
        }
    },

    addItemsToCart: (payload, id) => {
        return {
            type: handleSales.ADD_ITEMS_TO_CART,
            payload,
            id: id,
        }
    },
    removeItemsFromCart: (payload, id) => {
        return {
            type: handleSales.REMOVE_ITEMS_FROM_CART,
            payload,
            id: id,
        }
    },
    calcItemsToCart: (value, payload) => {
        let newPayload = { ...payload };
        delete newPayload.id;
        return {
            type: handleSales.CALC_ITEMS_TO_CART,
            payload: newPayload,
            value,
            id: payload.id,
        }
    },
    clearItemsFromCart: payload => {
        return {
            type: handleSales.CLEAR_ITEMS_FROM_CART,
            payload,
        }
    },
    toggleCheckoutModal: payload => {
        
        return {
            type: handleSales.TOGGLE_CHECKOUT_MODAL,
            payload,
        }
    },
    submitCheckoutData: payload => {
        return {
            type: handleSales.SUBMIT_CHECKOUT_DATA,
            payload,
        }
    },
//CLEAR MESSAGE POPUPS FOR SALES
    clearError: ()=>{
        return {
            type: handleSales.CLEAR_ERROR
        }
    },
    clearSuccess: ()=>{
        return {
            type: handleSales.CLEAR_SUCCESS
        }
    },
    clearInfo: ()=>{
        return {
            type: handleSales.CLEAR_INFO
        }
    },
    clearAllMessages: ()=>{
        return {
            type: handleSales.CLEAR_ALL_MESSAGES
        }
    },

}

export default handleSales;