const getStats = {
    //transactions :: string values ===  reducer keys (Don't change!)
    GET_NUMBER_TRANSACTIONS: 'numTransactions',
    //products
    GET_NUMBER_PRODUCTS: 'numProducts',
    //services
    GET_NUMBER_SERVICES: 'numServices',
    //customers
    GET_NUMBER_CUSTOMERS: 'numCustomers',
    //vendors
    GET_NUMBER_VENDORS: "numVendors",
    //users
    GET_NUMBER_USERS: "numUsers",
    //categories
    GET_NUMBER_CATEGORIES: "numCategories",
    //locations
    GET_NUMBER_LOCATIONS: "numLocations",
    //revenue-current-month
    GET_REVENUE_MONTH: "numRevenueMonth",
    //revenue-prev-month
    GET_REVENUE_PREV_MONTH: "numRevenuePrevMonth",
    //revenue-total-YTD
    GET_REVENUE_YTD: "numRevenueYTD",
    //transactions-by-user
    GET_TRANSACTIONS_USER: "numTransactionsUser",
    //any
    GET_NUMBER_ITEMS_SUCCESS: "GET_NUMBER_ITEMS_SUCCESS",
    GET_NUMBER_ITEMS_FAIL: "GET_NUMBER_ITEMS_FAIL",

    //TRANSACTIONS
    getTransactions: payload => {
        return { type: getStats.GET_NUMBER_TRANSACTIONS, payload, }
    },
    //PRODUCTS
    getNumProducts: payload => {
        return { type: getStats.GET_NUMBER_PRODUCTS, payload, }
    },
    //SERVICES
    getNumServices: payload => {
        return { type: getStats.GET_NUMBER_SERVICES, payload, }
    },
    //CUSTOMERS
    getNumCustomers: payload => {
        return { type: getStats.GET_NUMBER_CUSTOMERS, payload, }
    },
    //VENDORS
    getNumVendors: payload => {
        return { type: getStats.GET_NUMBER_VENDORS, payload, }
    },
    //USERS
    getNumUsers: payload => {
        return { type: getStats.GET_NUMBER_USERS, payload, }
    },
    //CATEGORIES
    getNumCategories: payload => {
        return { type: getStats.GET_NUMBER_CATEGORIES, payload, }
    },
    //LOCATIONS
    getNumLocations: payload => {
        return { type: getStats.GET_NUMBER_LOCATIONS, payload, }
    },
    //REVENUE MONTH
    getNumRevenueMonth: payload => {
        return { type: getStats.GET_REVENUE_MONTH, payload, }
    },
    //REVENUE PREVIOUS MONTH
    getNumRevenuePrevMonth: payload => {
        return { type: getStats.GET_REVENUE_PREV_MONTH, payload, }
    },
    //REVENUETOTALYTD
    getNumRevenueYTD: payload => {
        return { type: getStats.GET_REVENUE_YTD, payload, }
    },
    //TRANSACTIONSBYUSER YTD
    getTransactionsByUser: payload => {
        return { type: getStats.GET_TRANSACTIONS_USER, payload, }
    },
    //ANY-ITEMS
    getItemsSuccess: (payload, data) => {
        return {
            type: getStats.GET_NUMBER_ITEMS_SUCCESS,
            payload: payload,
            data: data,
        }
    },
    getItemsFail: payload => {
        return {
            type: getStats.GET_NUMBER_ITEMS_FAIL,
            payload,
        }
    },
}

export default getStats;